import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from "@angular/common/http";

import {  Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';

//import { CompanyMaster } from './CompanyMaster';
import { CompanyMaster } from '../models/UserData';
import { APIDetails } from '../models/AllConstansts';

@Injectable({
  providedIn: 'root'
})
export class CompanyMasterDetailsService {

  private apiServer = APIDetails.HelathAPI; //"https://localhost:44380"; //
  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json'
    })
  }
  constructor(private httpClient: HttpClient) { }

  create(CompanyMaster): Observable<CompanyMaster> {
    return this.httpClient.post<CompanyMaster>(this.apiServer + '/CompanyMaster/', JSON.stringify(CompanyMaster), this.httpOptions)
    .pipe(
      catchError(this.errorHandler)
    )
  }  
  getById(id): Observable<CompanyMaster> {
    return this.httpClient.get<CompanyMaster>(this.apiServer + '/CompanyMaster/' + id)
    .pipe(
      catchError(this.errorHandler)
    )
  }

  getAll(): Observable<CompanyMaster[]> {
    return this.httpClient.get<CompanyMaster[]>(this.apiServer + '/CompanyMaster/')
    .pipe(
      catchError(this.errorHandler)
    )
  }

  createStudent(employee: CompanyMaster): Observable<CompanyMaster> {
    
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) };
    return this.httpClient.post<CompanyMaster>(this.apiServer + '/CompanyMaster/', employee, httpOptions);
  
  }

  update(id, CompanyMaster): Observable<CompanyMaster> {
    return this.httpClient.put<CompanyMaster>(this.apiServer + '/CompanyMaster/' + id, JSON.stringify(CompanyMaster), this.httpOptions)
    .pipe(
      catchError(this.errorHandler)
    )
  }

  // updateStudent(id:string,employee: UserRegistartion): Observable<UserRegistartion> {
  //   const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) };
  //   return this.http.put<UserRegistartion>(this.apiURL+ '?idt='+id, employee, httpOptions);
  // }

  delete(id){
    return this.httpClient.delete<CompanyMaster>(this.apiServer + '/CompanyMaster/' + id, this.httpOptions)
    .pipe(
      catchError(this.errorHandler)
    )
  }
  errorHandler(error) {
     let errorMessage = '';
     if(error.error instanceof ErrorEvent) {
       // Get client-side error
       errorMessage = error.error.message;
     } else {
       // Get server-side error
       errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
     }
     console.log(errorMessage);
     return throwError(errorMessage);
  }
}
