  

 import { Injectable } from '@angular/core';
 import { HttpClient, HttpErrorResponse, HttpHeaders } from "@angular/common/http";
 
 import {  Observable, throwError } from 'rxjs';
 import { catchError } from 'rxjs/operators';
 
 //import { Vaccinations } from './Vaccinations';
 import { Vaccinations } from '../models/UserData';
 import { APIDetails } from '../models/AllConstansts';
 
 @Injectable({
   providedIn: 'root'
 })
 export class VaccinationsCategoryService  {
 
   private apiServer = APIDetails.HelathAPI; //"https://localhost:44380";
   httpOptions = {
     headers: new HttpHeaders({
       'Content-Type': 'application/json'
     })
   }
   constructor(private httpClient: HttpClient) { }
 
   create(Vaccinations): Observable<Vaccinations> {
     return this.httpClient.post<Vaccinations>(this.apiServer + '/Vaccinations/', JSON.stringify(Vaccinations), this.httpOptions)
     .pipe(
       catchError(this.errorHandler)
     )
   }  
   getById(id): Observable<Vaccinations> {
     return this.httpClient.get<Vaccinations>(this.apiServer + '/Vaccinations/' + id)
     .pipe(
       catchError(this.errorHandler)
     )
   }
 
   getAll(): Observable<Vaccinations[]> {
     return this.httpClient.get<Vaccinations[]>(this.apiServer + '/Vaccinations/')
     .pipe(
       catchError(this.errorHandler)
     )
   }
 
   createStudent(employee: Vaccinations): Observable<Vaccinations> {
     
     const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) };
     return this.httpClient.post<Vaccinations>(this.apiServer + '/Vaccinations/', employee, httpOptions);
   
   }
 
   update(id, Vaccinations): Observable<Vaccinations> {
     return this.httpClient.put<Vaccinations>(this.apiServer + '/Vaccinations/' + id, JSON.stringify(Vaccinations), this.httpOptions)
     .pipe(
       catchError(this.errorHandler)
     )
   }
 
   // updateStudent(id:string,employee: UserRegistartion): Observable<UserRegistartion> {
   //   const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) };
   //   return this.http.put<UserRegistartion>(this.apiURL+ '?idt='+id, employee, httpOptions);
   // }
 
   delete(id){
     return this.httpClient.delete<Vaccinations>(this.apiServer + '/Vaccinations/' + id, this.httpOptions)
     .pipe(
       catchError(this.errorHandler)
     )
   }
   errorHandler(error) {
      let errorMessage = '';
      if(error.error instanceof ErrorEvent) {
        // Get client-side error
        errorMessage = error.error.message;
      } else {
        // Get server-side error
        errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
      }
      console.log(errorMessage);
      return throwError(errorMessage);
   }
 }
 