
import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from "@angular/common/http";

import {  Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';

//import { GroupTests } from './GroupTests';
import { GroupTests } from '../models/UserData';
import { APIDetails } from '../models/AllConstansts';

@Injectable({
  providedIn: 'root'
})
export class GrouTestService {

  private apiServer = APIDetails.HelathAPI; //"https://localhost:44380";
  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json'
    })
  }
  constructor(private httpClient: HttpClient) { }

  create(GroupTests): Observable<GroupTests> {
    return this.httpClient.post<GroupTests>(this.apiServer + '/GroupTests/', JSON.stringify(GroupTests), this.httpOptions)
    .pipe(
      catchError(this.errorHandler)
    )
  }  
  getById(id): Observable<GroupTests> {
    return this.httpClient.get<GroupTests>(this.apiServer + '/GroupTests/' + id)
    .pipe(
      catchError(this.errorHandler)
    )
  }

  getAll(): Observable<GroupTests[]> {
    return this.httpClient.get<GroupTests[]>(this.apiServer + '/GroupTests/')
    .pipe(
      catchError(this.errorHandler)
    )
  }

  createStudent(employee: GroupTests): Observable<GroupTests> {
    
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) };
    return this.httpClient.post<GroupTests>(this.apiServer + '/GroupTests/', employee, httpOptions);
  
  }

  update(id, GroupTests): Observable<GroupTests> {
    return this.httpClient.put<GroupTests>(this.apiServer + '/GroupTests/' + id, JSON.stringify(GroupTests), this.httpOptions)
    .pipe(
      catchError(this.errorHandler)
    )
  }

  // updateStudent(id:string,employee: UserRegistartion): Observable<UserRegistartion> {
  //   const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) };
  //   return this.http.put<UserRegistartion>(this.apiURL+ '?idt='+id, employee, httpOptions);
  // }

  delete(id){
    return this.httpClient.delete<GroupTests>(this.apiServer + '/GroupTests/' + id, this.httpOptions)
    .pipe(
      catchError(this.errorHandler)
    )
  }
  errorHandler(error) {
     let errorMessage = '';
     if(error.error instanceof ErrorEvent) {
       // Get client-side error
       errorMessage = error.error.message;
     } else {
       // Get server-side error
       errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
     }
     console.log(errorMessage);
     return throwError(errorMessage);
  }
}
